___CSS_LOADER_EXPORT___.push([module.id, ".fade:not(.show){opacity:1}", "",{"version":3,"sources":["webpack://./resources/js/components/currencies/EditCurrency.vue"],"names":[],"mappings":"AAqKA,iBACA,SACA","sourcesContent":["<template>\r\n  <div class=\"modal\" id=\"edit-currency\">\r\n    <div class=\"modal-dialog\">\r\n      <div class=\"modal-content\">\r\n        <!-- Modal Header -->\r\n        <div class=\"modal-header\">\r\n          <h4 class=\"modal-title\">Edit Currency</h4>\r\n          <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n            &times;\r\n          </button>\r\n        </div>\r\n\r\n        <!-- Modal body -->\r\n        <div class=\"modal-body\">\r\n          <validation-observer ref=\"editCurrency\">\r\n            <form @submit.prevent=\"editCurrency\" ref=\"form\">\r\n              <div class=\"form-row\">\r\n                <div class=\"col-md-12\">\r\n                  <validation-provider\r\n                    name=\"Currency Name\"\r\n                    :rules=\"{ required: true, min: 2, max: 20 }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Currency Name</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter currency name e.g US Dollar\"\r\n                        aria-describedby=\"Name-feedback\"\r\n                        v-model.trim=\"currency.currency_name\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div id=\"Name-feed back\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n                  <!-- currency code -->\r\n                  <validation-provider\r\n                    name=\"Currency Code\"\r\n                    :rules=\"{ required: true }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Currency Code</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter currency Code e.g USD\"\r\n                        aria-describedby=\"Code-feedback\"\r\n                        v-model.trim=\"currency.currency_code\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div id=\"Code-feed back\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <!-- currency symbol -->\r\n                  <validation-provider\r\n                    name=\"Currency Symbol\"\r\n                    :rules=\"{ required: true }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Currency Symbol</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter currency name e.g $\"\r\n                        aria-describedby=\"Symbol-feedback\"\r\n                        v-model.trim=\"currency.currency_symbol\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div id=\"Symbol-feed back\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <div class=\"form-group\">\r\n                    <div class=\"form-check\">\r\n                      <label class=\"form-check-label\">\r\n                        <input\r\n                          class=\"form-check-input\"\r\n                          type=\"checkbox\"\r\n                          id=\"status\"\r\n                          v-model=\"currency.status\"\r\n                        />\r\n                        Status\r\n                      </label>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n\r\n              <div class=\"col-12 my-3 text-right\">\r\n                <button class=\"btn btn-primary\" type=\"submit\">Submit</button>\r\n              </div>\r\n            </form>\r\n          </validation-observer>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  emits: [\"update\"],\r\n  props: {\r\n    currency: {\r\n      type: Object,\r\n      required: true,\r\n    },\r\n  },\r\n  data() {\r\n    return {};\r\n  },\r\n  methods: {\r\n    editCurrency() {\r\n      this.$refs.editCurrency.validate().then((valid) => {\r\n        if (valid) {\r\n          //submit data\r\n          this.$store.dispatch(\"startLoading\");\r\n          axios\r\n            .post(`/admin/currencies/${this.currency.id}`, {\r\n              ...this.currency,\r\n              _method: \"PUT\",\r\n            })\r\n            .then((res) => {\r\n              //   this.$store.dispatch(\"stopLoading\");\r\n              this.closeModal();\r\n\r\n              this.$nextTick(() => {\r\n                this.$emit(\"update\");\r\n                this.showSuccessToast('Currency edited successfully')\r\n              });\r\n            })\r\n            .catch((res) => {\r\n              this.$store.dispatch(\"stopLoading\");\r\n              this.showErrorToast('There was a problem editing the currency')\r\n            });\r\n        }\r\n      });\r\n    },\r\n    //---- Validation State Form\r\n    getValidationState({ dirty, validated, valid = null }) {\r\n      if (dirty || validated) {\r\n        return valid ? \"is-valid\" : \"is-invalid\";\r\n      }\r\n      return \"\";\r\n    },\r\n    closeModal() {\r\n      $(\"#edit-currency\").modal(\"hide\");\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style>\r\n.fade:not(.show) {\r\n  opacity: 1;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
